import React from 'react';
import NotAllowedMessage from './not_allowed_message';

const NotAllowed = ({
  MainLayout,
}) => (
  MainLayout ?
    <MainLayout content={() => <NotAllowedMessage />} /> :
    <NotAllowedMessage />
);

NotAllowed.propTypes = {
  MainLayout: React.PropTypes.element,
};

export default NotAllowed;
